input.text {
  font-size: inherit;
  font-family: inherit;
  border: unset;
  background: unset;
  align-self: center;
  margin: 0em 1em;
  padding: 0;
  border-bottom: solid rgb(143, 134, 134) 1px;
}
.bttn {
  display: block;
  width: 2.5em;
  background-size: 1em;
  background-position: center;
  background-repeat: no-repeat;
  cursor: pointer;
  flex-shrink: 0;
}
.confirm {
  background-image: url(../../img/check-solid.svg);
}
.abort {
  background-image: url(../../img/times-solid.svg);
  background-size: 0.9em;
  border-radius: 0 6px 6px 0;
}
.edit {
  background-image: url(../../img/pen-solid.svg);
}
.delete {
  background-image: url(../../img/trash-solid.svg);
  border-radius: 0 6px 6px 0;
  background-size: 0.9em;
}
.bttn:hover {
  background-color: rgba(0, 0, 0, 0.1);
}

#task-list > li.checked {
  background-color: rgba(125, 77, 77, 0.5) !important;
  padding: 0 0 0 40px;
  opacity: .7;
}

#task-list > li.checked > .text {
  text-decoration: line-through;
  font-style: italic;
  font-size: .9em;
}

#task-list > li.checked > .edit {
  display: none;
}
